import { Button } from "react-bootstrap";
import "../../css/profile/InfoContent.css"
import { styled } from "styled-components";
import { useState } from "react";

const ToggleContainer = styled.div`
  position: relative;
  cursor: pointer;

  > .toggle-container {
    width: 50px;
    height: 24px;
    border-radius: 30px;
    border: 1px solid gray;
    background-color: rgb(233,233,234);}
    
    //.toggle--checked     CSS 
  > .toggle--checked {
    background-color: rgb(0,200,102);
    transition : 0.5s
    
  }

  > .toggle-circle {
    position: absolute;
    top: 1px;
    left: 1px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: rgb(255,254,255);
    transition : 0.5s
    
    //.toggle--checked     CSS 
  } >.toggle--checked {
    left: 27px;
    transition : 0.5s
  }
`;

export const Toggle = () => {
  const [isOn, setisOn] = useState(false);

  const toggleHandler = () => {
    // isOn    
    setisOn(!isOn)
  };

  return (
    <>
      <ToggleContainer onClick={toggleHandler}>
        <div className={`toggle-container ${isOn ? "toggle--checked" : null}`}/>
        <div className={`toggle-circle ${isOn ? "toggle--checked" : null}`}/>
      </ToggleContainer>
    </>
  );
};

export function InfoContent() {

    return (
        <div className="container">
            <div className="container-header-tab">
                <div className="container-header">
                    <p className="container-header-text"> <label> </label></p>
                </div>
                <div>
                    <Button variant="outline-secondary"> </Button>
                </div>
                <div className="info-tab-first">
                    <label>  : </label>
                </div>
                <div className="info-tab">
                    <label>  : </label>
                </div>
            </div>
            <div className="container-header-tab">
                <div className="container-header">
                    <p className="container-header-text"> <label> </label> </p>
                </div>
                <div className="info-tab-first">
                    <label>       </label> <Toggle/>
                </div>
            </div>
            <div className="container-header-tab">
                <div className="container-header">
                    <p className="container-header-text">    </p>
                </div>
                <div>
                    <Button variant="outline-secondary"></Button>
                </div>
                <div className="container-addr">
                    <div>
                        <p>  .</p>
                    </div>
                </div>
            </div>
            <div className="container-header-tab">
                <div className="container-header">
                    <p className="container-header-text"> <label> </label> </p>
                </div>
                <div className="info-tab-first">
                    <label>      </label> <Toggle/>
                </div>
                <div className="info-tab-first">
                    <label>     </label> <Toggle/>
                </div>
            </div>
        </div>
    );
}