import { useEffect, useRef, useState } from "react";
import { Button, Dropdown } from "react-bootstrap";
import { styled } from "styled-components";
import {
    PaymentWidgetInstance,
    loadPaymentWidget,
    ANONYMOUS,
  } from "@tosspayments/payment-widget-sdk";

  const selector = "#payment-widget";
  const clientKey = "test_ck_D5GePWvyJnrK0W0k6q8gLzN97Eoq";
  const customerKey = "YbX2HuSlsC9uVJW6NMRMj";

const ShopOrderInfo = () => {

    const paymentWidgetRef = useRef(null);
    const paymentMethodsWidgetRef = useRef(null);
    const [price, setPrice] = useState(50000);
  
    useEffect(() => {
      (async () => {
        // ------    ------
        //   customerKey  ANONYMOUS .
        const paymentWidget = await loadPaymentWidget(clientKey, customerKey); //  
        // const paymentWidget = await loadPaymentWidget(clientKey, ANONYMOUS); //  
  
        // ------    ------
        // https://docs.tosspayments.com/reference/widget-sdk#renderpaymentmethods---
        const paymentMethodsWidget = paymentWidget.renderPaymentMethods(
          selector,
          { value: price }
        );
  
        // ------    ------
        // https://docs.tosspayments.com/reference/widget-sdk#renderagreement
        paymentWidget.renderAgreement("#agreement");
  
        paymentWidgetRef.current = paymentWidget;
        paymentMethodsWidgetRef.current = paymentMethodsWidget;
      })();
    }, []);
  
    useEffect(() => {
      const paymentMethodsWidget = paymentMethodsWidgetRef.current;
  
      if (paymentMethodsWidget == null) {
        return;
      }
  
      // ------   ------
      // https://docs.tosspayments.com/reference/widget-sdk#updateamount-
      paymentMethodsWidget.updateAmount(
        price,
        paymentMethodsWidget.UPDATE_REASON.COUPON
      );
    }, [price]);

    
    return (
        <OrderInfoMain>
            <ShopOrderInfoDiv className="shop-order-info">
                <OrderInfoHeader>
                    <span></span>
                </OrderInfoHeader>

                <div className="order-content-address">
                    <div className="order-address-select">
                        <span></span>
                        <Button></Button>
                    </div>
                    <hr/>
                    <div className="address-info">
                        <span> </span>
                        <span></span>
                        <span>000-0000-0000</span>
                        <Dropdown> </Dropdown>
                    </div>
                </div>

                <div className="order-content-item">
                    <div className="item-info-header">
                        <span></span>
                    </div>
                    <hr/>
                    <div>
                        <div></div>
                        <div>
                            <span> </span>
                            <span> </span>
                            <span> </span>
                        </div>
                        <div>
                            <span></span>
                        </div>
                        <div>
                            <span></span>
                        </div>
                        <div>
                            <span></span>
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="oreder-content-discount">
                    <div className="discount-info-header">
                        <span>/</span>
                    </div>

                    <div>
                        <span></span> <Button></Button>
                    </div>
                    
                    <div>
                        <span> </span> <input type="text"></input><Button></Button>
                        <span>  </span>
                    </div>
                </div>
            </ShopOrderInfoDiv>
            <Vline class='v-line'></Vline>
            <InfoSide className="order-info-side">
                <SideOrderPay>
                <div>
      <div>
        <label>
          <input
            type="checkbox"
            onChange={(event) => {
              setPrice(event.target.checked ? price - 5000 : price + 5000);
            }}
          />
        </label>
      </div>
      <div id="payment-widget" />
      <div id="agreement" />
      <OrderPayPrice>
      <span>  : {`${price.toLocaleString()}`}</span>
      <Button
        onClick={async () => {
          const paymentWidget = paymentWidgetRef.current;

          try {
            // ------ ''     ------
            // https://docs.tosspayments.com/reference/widget-sdk#requestpayment-
            await paymentWidget?.requestPayment({
              orderId: "IOASD90ASD723HASD98AXC6ASD9A6SD",
              orderName: "   2",
              customerName: "",
              customerEmail: "customer123@gmail.com",
              successUrl: `${window.location.origin}/success`,
              failUrl: `${window.location.origin}/fail`,
            });
          } catch (error) {
            //  
            console.error(error);
          }
        }}
      >
        
      </Button>
      </OrderPayPrice>
    </div>
                </SideOrderPay>
            </InfoSide>
        </OrderInfoMain>
    );
}

const Vline = styled.div`
    border-left : 1px solid #c7c8c9;
    height : auto;
    padding : 10px;
`

const ShopOrderInfoDiv = styled.div`
    width: 800px;
    
`

const InfoSide = styled.div`
    display: flex;
    height: auto;
    width: 460px;
    align-items: flex-end;
`

const SideOrderPay = styled.div`
    width: 460px;
`

const OrderInfoMain = styled.div`
    display: flex;
    justify-content: space-between;
`;

const OrderInfoHeader = styled.div`
    height: 50px;
    font-weight: bold;
    font-size: 25px;
`;

const OrderPayPrice = styled.div`
    display: flex;
    justify-content: space-between;

`

const OrderAddressInfo = styled.div`
    display: flex;
    flex-direction: column;
`

export default ShopOrderInfo;