import { Fragment } from "react";
import { Button, Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { styled } from "styled-components";

const SingForm = () => {
  const { handleSubmit, register, formState: { errors }, getValues } = useForm();

  const onSubmit = (data) => {
    console.log(data); //     .
    
  };

  return (
    <Fragment>
      <SignFormMother className="sign-form-home">
        <SignFormContents className="sign-form-contents">
          <h2></h2>
          <form onSubmit={handleSubmit(onSubmit)}> {/* form   onSubmit  handleSubmit . */}
            <CustomFormGroup>
            <Form.Control
                type="text"
                placeholder=""
                name="email"
                {...register("email", {
                    required: true,
                    pattern: /^\S+@\S+$/i
                })}
            />
              {errors.email && errors.email.type === "required" && (
                <ErrorMessage>   .</ErrorMessage>
              )}
              {errors.email && errors.email.type === "pattern" && (
                <ErrorMessage>   .</ErrorMessage>
              )}
            <Form.Control
              type="password"
              placeholder=""
              name="password"
              {...register("password", {
                  required: true,
                  minLength: 6
              })}
            />
              {errors.password && errors.password.type === "required" && (
                <ErrorMessage>   .</ErrorMessage>
              )}
              {errors.password && errors.password.type === "minLength" && (
                <ErrorMessage>  6  .</ErrorMessage>
              )}
            <Form.Control
              type="password"
              placeholder=" "
              name="confirmPassword"
              {...register("confirmPassword", {
                  required: true,
                  validate: (value) =>
                  value === getValues("password") || "  ."
              })}
            />
              {errors.confirmPassword && errors.confirmPassword.type === "required" && (
                <ErrorMessage>   .</ErrorMessage>
              )}
              {errors.confirmPassword && errors.confirmPassword.type === "validate" && (
                <ErrorMessage>{errors.confirmPassword.message}</ErrorMessage>
              )}
            </CustomFormGroup>
            <div className="content-item-button">
              <LoginButton style={{ width: '350px' }} type="submit"></LoginButton> {/*   type="submit"    . */}
            </div>
          </form>
        </SignFormContents>
      </SignFormMother>
    </Fragment>
  );
}

const SignFormContents = styled.div`
    width: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const SignFormMother = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 450px;
`;

const CustomFormGroup = styled(Form.Group)`
    display: flex;
    height: 200px;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
`;

const LoginButton = styled(Button)`
    --bs-btn-color: #000;
    --bs-btn-bg: #ffffff;
    --bs-btn-border-color: #000000;
`;

const ErrorMessage = styled.div`
  color: red;
  font-size: 0.8em;
  margin-top: 5px;
`;

export default SingForm;